import React from "react";
import logoImage from "../images/logo4.png"; // Adjust path to your logo

export default function Navbar() {
  return (
    <nav className="bg-pastel-lavender max-w-xl mx-auto flex items-center justify-start py-6 px-6 shadow-lg border-b-2 border-white/20 rounded-b-lg">
      <div className="flex items-center space-x-3">
        <a href="/" title="Go to Home Page">
          <img src={logoImage} alt="One Love Grooming Logo" className="h-8 w-auto object-contain transition-transform duration-300 group-hover:scale-105" />
        </a>
        <a
          href="/services"
          className="block w-full py-2 px-4 bg-white text-gray-900 rounded-full text-center font-semibold hover:bg-gray-100 hover:text-pastel-lavender transition-colors"
        >
          Services
        </a>
        <a
          href="/about"
          className="block w-full py-2 px-4 bg-white text-gray-900 rounded-full text-center font-semibold hover:bg-gray-100 hover:text-pastel-lavender transition-colors"
        >
          About
        </a>
        <a
          href="/contact"
          className="block w-full py-2 px-4 bg-white text-gray-900 rounded-full text-center font-semibold hover:bg-gray-100 hover:text-pastel-lavender transition-colors"
        >
          Contact
        </a>
        <a
          href="/quotes"
          className="block w-full py-2 px-4 bg-white text-gray-900 rounded-full text-center font-semibold hover:bg-gray-100 hover:text-pastel-lavender transition-colors"
        >
          Quote
        </a>
      </div>
    </nav>
  );
}